import { verifyRequestResponsePair, } from '@dfinity/response-verification';
export const maxCertTimeOffsetNs = BigInt.asUintN(64, BigInt(300000000000));
export function responseVerification(httpRequest, httpResponse, minAllowedVerificationVersion, canisterId, rootKey) {
    const currentTimeNs = BigInt.asUintN(64, BigInt(Date.now() * 1000000)); // from ms to nanoseconds
    return verifyRequestResponsePair({
        headers: httpRequest.headers,
        method: httpRequest.method,
        url: httpRequest.url,
        body: httpRequest.body,
    }, {
        statusCode: httpResponse.status_code,
        body: httpResponse.body,
        headers: httpResponse.headers,
    }, canisterId.toUint8Array(), currentTimeNs, maxCertTimeOffsetNs, new Uint8Array(rootKey), minAllowedVerificationVersion);
}
